@use 'variables' as *;
@use 'mixin' as *;
//   ( )
:root {
  --ck-size: 16px;
  --ck-size-lg: 24px;
  --ck-border: 1px;
  --ck-radius: 4px;
  --ck-color: #4D24C3;       //  
  --ck-border-color: #4D24C3; //  
  --ck-focus: 3px rgb(77, 36, 195, 0.15);
  --ck-disabled: #f2f2f2;
}

//  
.ckb {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  user-select: none;
  // line-height: 1.1;
  font-size: 14px;
  color: $normalText;

  //   ( /  )
  input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }

  //  
  &__box {
    width: var(--ck-size);
    height: var(--ck-size);
    min-width: var(--ck-size);
    border-radius: var(--ck-radius);
    box-sizing: border-box;
    border: var(--ck-border) solid var(--ck-border-color);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: background-color 150ms ease, border-color 150ms ease, transform 120ms ease;
    background: #fff;
    position: relative;
  }
  &.size-lg{
    .ckb__box{
      width: var(--ck-size-lg);
      height: var(--ck-size-lg);
    }
    input[type="checkbox"]:checked + .ckb__box {
      &::after {
        transform: rotate(-45deg) scale(1.5) translate(1px, -1px);
      }
    }
  }

  //   ( ::after )
  &__box::after {
    content: "";
    width: calc(var(--ck-size) * 0.5);
    height: calc(var(--ck-size) * 0.26);
    border-left: 1px solid transparent;
    border-bottom: 1px solid transparent;
    border-radius: 1px;
    transform: rotate(-45deg) scale(1);
    opacity: 0;
    transition: transform 140ms cubic-bezier(.2,.9,.3,1), opacity 140ms;
  }

  //   (input   )
  input[type="checkbox"]:focus + .ckb__box {
    box-shadow: 0 0 0 var(--ck-focus);
    outline: none;
  }

  // checked : ,  
  input[type="checkbox"]:checked + .ckb__box {
    background: var(--ck-color);
    border-color: var(--ck-color);

    &::after {
      border-color: #fff;
      transform: rotate(-45deg) scale(1) translate(1px, -1px);
      opacity: 1;
    }
    
  }

  // hover  (  )
  &:hover input[type="checkbox"] + .ckb__box {
    // transform: translateY(-1px);
  }

  // disabled 
  input[type="checkbox"]:disabled + .ckb__box {
    background: var(--ck-disabled);
    border-color: #e6e6e6;
    cursor: not-allowed;
    opacity: 0.9;
  }
  input[type="checkbox"]:disabled ~ .ckb__label {
    opacity: 0.6;
    cursor: not-allowed;
  }

  // indeterminate :    
  input[type="checkbox"].indeterminate + .ckb__box {
    background: var(--ck-color);
    border-color: var(--ck-color);

    &::after {
      /*    */
      width: calc(var(--ck-size) * 0.5);
      height: 2px;
      border: none;
      background: #fff;
      transform: scale(1);
      opacity: 1;
    }
  }

  // :      (  input )
  &__label {
    cursor: pointer;
    &.required{
      @include required;
    }
  }
}
