@use 'variables' as *;
:root{
  --content-bg: #F7F8F9;
}
.eva-preview-for-member{
  .layout{
    display: flex;
    height: 55vh;
    gap: 20px;
    justify-content: space-between;
    &__left{
      .gray-box{
        background-color: var(--content-bg);
        padding: 10px;
        &__topper{
          margin-bottom: 24px;
        }
      }
      flex: 1 1 50%;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 8px; /*    */
      }
  
      /*     */
      &::-webkit-scrollbar-track {
        background: #E9E9E9; /*    */
        border-radius: 20px;
      }
  
      /*     */
      &::-webkit-scrollbar-thumb {
        background: #888; /*    */
        border-radius: 20px;
      }
  
      /*        */
      &::-webkit-scrollbar-thumb:hover {
        background: #919191; /*    */
      }
    }
    &__right{
      flex: 1 1 50%;
      overflow-y: auto;
      padding-right: 10px;
      &::-webkit-scrollbar {
        width: 8px; /*    */
      }
  
      /*     */
      &::-webkit-scrollbar-track {
        background: #E9E9E9; /*    */
        border-radius: 20px;
      }
  
      /*     */
      &::-webkit-scrollbar-thumb {
        background: #888; /*    */
        border-radius: 20px;
      }
  
      /*        */
      &::-webkit-scrollbar-thumb:hover {
        background: #919191; /*    */
      }
    }
    &__divider{
      height: 100%;
      width: 1px;
      border-right: 1px solid #5E5E5E;
    }
  }
  .question-card{
    border-radius: 0;
    padding: 24px 0;
    border: 0;
    border-bottom: 1px solid $cardBorderColor;
    &:nth-last-of-type(1){
      border-bottom: 0;
    }
  }
  .eva-target__tabs{
    margin-top: 14px;
  }
  .eva-file-swiper{
    width: calc(100% + 20px);
    margin-left: -10px;
    margin-top: -10px;
    .swiper-pagination{
      position: relative;
    }
    .button-next, .button-prev{
      display: inline-flex;
      position: relative;
      align-items: center;
      justify-content: center;
      background-color: #FFF;
      border-radius: 50%;
      top: unset;
      width: 30px;
      height: var(--nav-button-height);
      z-index: 11;
      box-shadow: 0 1px 4px 0 rgba(122,83,236,0.20);
      color: $primaryColor;
      cursor: pointer;
      &.swiper-button-disabled{
        color: #B0B0B0;
      }
    }
    .button-next, .button-prev{
      left: unset;
      right: unset;
    }
    .nav-wrap{
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
      gap: 24px;
      .swiper-pagination{
        width: auto;
      }
    }
    
  }
  .divider_hr_24{
    border-top: 1px solid #BDBDBD;
  }
}