/*    */
.view-toggle {
  display: inline-flex;
  border: 1px solid #000;
  border-radius: 8px;
  overflow: hidden;
  height: 42px;
  
  /*    */
  .view-tab {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    gap: 6px;
    font-size: 15px;
    cursor: pointer;
    color: #fff;
    background: #000;
    transition: all 0.2s;
    user-select: none;
  }

  /* active  -  ,   */
  input[type="radio"]:checked+.view-tab {
    background: #fff;
    color: #000;
  }


}

/* input  */
.view-toggle input[type="radio"] {
  display: none;
}


/*   ( ) */
.view-tab+input+label {
  border-left: 1px solid #000;
}

/* hover    */
.view-tab:hover {
  filter: brightness(1.2);
}