@use 'variables' as *;
.divider {
  width: 1px;
  height: 16px;
  border: none;
  background: var(--color-gray-light);
  flex-shrink: 0;
  
}
.divider_hr {
  display: block;
  margin: 40px 0 !important;
  border: none;
  height: 1px;
  width: 100%;
  border-top: 1px solid var(--color-gray-light); /*     */
  &_24{
    border-top: 1px solid var(--color-gray-light); /*     */
    width: 100%;
    margin: 24px 0 !important;
  }
}