@use 'variables'as *;

//   ( )
:root {
  --ck-size: 16px;
  --ck-size-lg: 24px;
  --ck-border: 1px;
  --ck-radius: 4px;
  --ck-color: #4D24C3; //  
  --ck-border-color: #4D24C3; //  
  --ck-focus: 3px rgb(77, 36, 195, 0.15);
  --ck-disabled: #f2f2f2;
}

.chips{
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  .chip {
  
    //   ( /  )
    input[type="checkbox"] {
      position: absolute;
      opacity: 0;
      width: 0;
      height: 0;
      margin: 0;
      padding: 0;
    }
  
    //  
    &__box {
      position: relative;
      display: inline-flex;
      width: auto;
      padding: 0 8px;
      height: 32px;
      border-radius: 30px;
      border: 1px solid #D9D9D9;
      background: #FFF;
      align-items: center;
      justify-content: center;
      transition: background-color 150ms ease, border-color 150ms ease, transform 120ms ease;
      color: $smallText;
      font-size: 14px;
    }
  
  
    // checked : ,  
    input[type="checkbox"]:checked+.chip__box {
      background: var(--ck-color);
      border-color: var(--ck-color);
      color: #FFF;
      &::after {
        border-color: #fff;
        transform: rotate(-45deg) scale(1) translate(1px, -1px);
        opacity: 1;
      }
  
    }
  
    // hover  (  )
    &:hover input[type="checkbox"]+.chip__box {
      // transform: translateY(-1px);
    }
  
    // disabled 
    input[type="checkbox"]:disabled+.chip__box {
      background: var(--ck-disabled);
      border-color: #e6e6e6;
      cursor: not-allowed;
      opacity: 0.9;
    }
  
  
  
    // :      (  input )
    &__label {
      cursor: pointer;
    }
  }
}