@use 'variables' as *;

/* CSS     */
:root{
  --search-height: 40px;
  --search-radius: 8px;
  --search-bg: $bgLightGray;
  --search-border-color: transparent; /* border:0  */
  --search-focus-ring: 0 0 0 4px rgba(11,116,222,0.12);
  --search-icon-color: #666;
  --search-btn-bg: transparent;
  --search-btn-hover-bg: rgba(0,0,0,0.04);
}

/*
//  
.visually-hidden {
  position: absolute !important;
  width: 1px; height: 1px;
  padding: 0; margin: -1px;
  overflow: hidden; clip: rect(0 0 0 0); white-space: nowrap; border: 0;
}
*/

/*  () */
.input-text-wrapper {
  position: relative;
  width: 100%;
  max-width: 500px; /*   */
  &__wide{
    position: relative;
    max-width: 100%;
    flex: 1;
  }
  &__sub{
    display: flex;
    margin-top: 8px;
    &_text{
      font-size: 12px;
      color: $guideColor;
    }
  }
  
}


/* For Chrome, Safari, Edge, Opera */
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; /* Important for some browsers to fully remove the space */
}

/* For Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/*    */
.input-text, .input-textarea, .input-text-calendar, .input-time {
  width: 100%;
  height: var(--search-height);
  padding: 0 14px; /*   /   */
  border: 1px solid transparent;
  background-color: $bgLightGray;
  border-radius: var(--search-radius);
  box-sizing: border-box;
  font-size: 14px;
  font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
  color: $Title_Dark;
  outline: none;
  transition: box-shadow .16s ease, transform .12s ease;
  /* optional subtle inner shadow to separate from background */
  &:disabled{
    background-color: $NormalDarkGray;
  }
  &.search{
    padding-right: 44px;
  }
}
.input-textarea{
  display: block;
  height: 100px;
  padding: 12px 16px;
  resize: vertical;
}
/*     () */
.input-text:focus, .input-textarea:focus {
  border: 1px solid $primaryColor;
}
.input-text-calendar{
  width: auto;
  padding: 0 16px 0 52px;
  background-image: url('../../images/icon_calendar.svg');
  background-repeat: no-repeat;
  background-position: left 16px center;
  &.range-picker{
    width: 352px;
  }
}

/*   ( ) */
.search-btn {
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  height: calc(var(--search-height) - 8px);
  width: calc(var(--search-height) - 8px);
  padding: 6px;
  border-radius: 6px;
  border: 0;
  background: var(--search-btn-bg);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--search-icon-color);
  transition: background-color .12s ease, color .12s ease, transform .08s ease;
}

/*  hover / active */
.search-btn:hover { background: var(--search-btn-hover-bg); }
.search-btn:active { transform: translateY(-50%) scale(.98); }

/*  focus (  ) */
.search-btn:focus { outline: none; box-shadow: var(--search-focus-ring); }

/* svg     */
.search-btn img { display: block; width: 100%; height: 100%; max-width: 18px; max-height: 18px; }

/* disabled   */
.input-text:disabled,
.search-btn:disabled {
  opacity: .6;
  cursor: not-allowed;
}

//   
.input-pw{
  position: relative;
  display: flex;
  input{
    padding-right: 40px;
    &+button.hide-pw-toggle{
      display: flex;
      width: 16px;
      height: 16px;
      border: 0;
      padding: 0;
      background-color: transparent;
      position: absolute;
      top: calc(50% - 8px);
      right: 16px;
      background-image: url('../../images/icon_pw_show.svg');
      background-repeat: no-repeat;
      background-position: center;
      &.hide{
        background-image: url('../../images/icon_pw_hide.svg');
      }
    }
  }
}